<template>
    <view class="photo-header">
        <i class="w_editor-icon w_editor-icon-add-fill" @click="activationAdd"></i>
        <template v-for="(item, i) in option">
            <w-select 
            :show.sync="item.active" 
            :options="item.options || []"
            :type="item.type" 
            @isActive="handelIsActive(i)"
            @change="change($event, item.type)">
                <template v-if="item.type == 'textAlign'">
                    <i class="w_editor-icon" :class="[getOptionDict(item.options, value[item.type] || 'w_editor-icon-alignLeft')]"></i>
                </template>
                <template v-else-if="item.type == 'margin'">
                    <template v-if="!value[item.type]">
                        <i class="w_editor-icon w_editor-icon-jiangefenxi"></i>
                    </template>
                    <template v-else>
                        {{getOptionDict(item.options, value[item.type] || item.value)}}
                    </template>
                </template>
                <template v-else>
