        methods: {
            handelIsActive(index) {
                const arr = [...this.option]
                arr.forEach(item => {
                    item.active = false
                })
                arr[index].active = true
                this.option = arr
            },
            getOptionDict(arr, value) {
                const find = arr.find(o => o.value == value) || {}
                return find.label
            },
            change(e, type) {
                const obj = JSON.parse(JSON.stringify(this.value))
                obj[type] = e
                this.$emit('input', obj)
            },
            activationAdd() {
                const arr = [...this.option]
                arr.forEach(item => {
                    item.active = false
                })
                this.option = arr
                const obj = JSON.parse(JSON.stringify(this.value))
                const date = new Date()
                obj.activation = date.getMinutes() + ':' + date.getSeconds()
                this.$emit('input', obj)
            },
        },
